.wrapper {
  .promo {
    margin-bottom: 80px;

    &__inner {
      background: var(--white-color);
      border-radius: 25px;
    }

    &__box {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    &__title {
      margin-bottom: 32px;
      font-family: Montserrat;
      font-weight: 700;
      font-size: 50px;
      line-height: 50px;
      color: var(--main-color);
    }

    &__description {
      margin-bottom: 60px;
      font-family: ArialMT;
      font-weight: 400;
      font-size: 24px;
      line-height: 100%;
      color: var(--main-color);
    }

    &__btn {
      font-family: Montserrat;
      font-weight: 500;
      font-size: 18px;
      line-height: 26px;
      color: var(--white-color);
      background: var(--blue-color);
      outline: none;
      border: none;
      border-radius: 30px;
      padding: 13.5px 62px;
      transition: all 0.3s ease;

      &:hover {
        background: var(--blue-color-hover);
      }
    }

    &__left {
      height: 100%;
    }

    &__info {
      padding: 50px 40px;
    }

    &__right {
      height: 100%;

      img {
        width: 551px;
        border-radius: 0 25px 25px 0;
        object-fit: cover;
      }
    }
  }

  .about {
    margin-bottom: 100px;

    &__box {
      margin-top: 30px;
    }

    &__btns {
      display: flex;
      justify-content: space-between;
      width: 602px;
      background: var(--blue-color);
      border-radius: 25px;
      padding: 5px;
      position: relative;
    }

    &__btn {
      flex: 1;
      background: transparent;
      border: none;
      color: var(--white-color);
      padding: 10px 20px;
      font-family: Montserrat;
      font-size: 18px;
      font-weight: 500;
      line-height: 26px;
      border-radius: 30px;
      transition: all 0.3s ease;
      z-index: 1;
      position: relative;
      cursor: pointer;

      &.active {
        background-color: var(--white-color);
        color: var(--blue-color);
        z-index: 2;
      }
    }

    &__blocks {
      margin-top: 20px;
      background: var(--white-color);
      border-radius: 25px;
      padding: 25px 25px 30px 25px;
    }

    &__block {
      &-title {
        margin-bottom: 15px;
        font-family: Montserrat;
        font-weight: 700;
        font-size: 14px;
        line-height: 100%;
        color: var(--main-color);
        text-transform: uppercase;

        &._warn {
          text-transform: unset;
          margin-top: 30px;
          margin-bottom: 0;
          line-height: 20px;
          color: var(--orange-color);
        }

        span {
          font-family: ArialMT;
          font-weight: 400;
          font-size: 16px;
          line-height: 100%;
          color: var(--main-color);
          text-transform: lowercase;
        }
      }

      &-list {
        margin-bottom: 25px;
        margin-left: 25px;

        &:last-child {
          margin-bottom: 0;
        }
      }

      &-item {
        font-family: ArialMT;
        font-weight: 400;
        font-size: 16px;
        line-height: 100%;
        color: var(--main-color);
        margin-bottom: 10px;
        list-style-type: disc;
        list-style-position: outside;
        padding-left: 5px;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }

  .services {
    margin-bottom: 100px;

    &__box {
      margin-top: 30px;
    }

    .accordion {
      display: flex;
      flex-direction: column;
      gap: 20px;

      &__item {
        background: var(--white-color);
        border-radius: 25px;
        overflow: hidden;

        &.active {
          .chevron {
            transform: rotate(0deg);
            fill: var(--blue-color);
          }
        }
      }

      &__header {
        padding: 32px 30px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        cursor: pointer;
        user-select: none;
      }

      &__title {
        font-family: ArialMT;
        font-weight: 700;
        font-size: 20px;
        line-height: 100%;
        color: var(--blue-color);
      }

      .chevron {
        transform: rotate(180deg);
        width: 34px;
        height: 20px;
        fill: var(--main-color);
        transition: all 0.3s ease;
      }

      &__content {
        max-height: 0;
        overflow: hidden;
        transition: max-height 0.9s ease, padding 0.3s ease;
        padding: 0 30px;

        &-item {
          display: flex;
          align-items: center;
          justify-content: space-between;
          margin-bottom: 20px;

          &:last-child {
            margin-bottom: 0;
          }
        }

        &-info {
          display: flex;
          align-items: center;
          justify-content: flex-start;
          column-gap: 20px;
        }

        &-icon {
          min-width: 16px;
          min-height: 16px;
          max-width: 16px;
          max-height: 16px;
          border: 1px solid var(--main-color);
          border-radius: 50%;
          background: transparent;
          display: inline-block;
        }

        &-text {
          font-family: ArialMT;
          font-weight: 400;
          font-size: 16px;
          line-height: 100%;
          color: var(--main-color);
        }

        &-price {
          margin-left: 20px;
          font-family: ArialMT;
          font-weight: 700;
          font-size: 20px;
          line-height: 100%;
          color: var(--red-color);
          white-space: nowrap;
        }
      }

      &__item.active .accordion__content {
        padding: 0 30px 30px;
        max-height: 1000px;
      }
    }
  }

  .tools {
    margin-bottom: 100px;

    &__box {
      margin-top: 30px;
      display: flex;
      align-items: flex-start;
      justify-content: center;
      position: relative;
    }

    &__banner {
      position: relative;
      z-index: 2;
      min-width: 363px;
      max-width: 363px;
      height: 500px;
      border-radius: 20px;
      padding: 30px 23px;
      background-color: var(--blue-color);
      position: relative;
      overflow: hidden;
      margin-right: 40px;

      &::after {
        content: "";
        position: absolute;
        bottom: 0;
        right: 0;
        width: 407px;
        height: 289px;
        background-image: url("../assets/img/tools/sun.png");
        background-size: contain;
        background-repeat: no-repeat;
        background-position: right bottom;
        pointer-events: none;
      }

      &-text {
        font-family: ArialMT;
        font-weight: 400;
        font-size: 18px;
        line-height: 100%;
        color: var(--white-color);
      }
    }

    &__slider {
      position: relative;
      flex: 1;
      min-width: 0;
      height: 472px;
      overflow: hidden;

      .swiper-wrapper {
        display: flex;
        width: 100%;
      }

      .swiper-slide {
        padding: 30px 27px;
        border-radius: 25px;
        background: var(--white-color);
      }
    }

    &__tags {
      display: flex;
      flex-wrap: wrap;
      gap: 10px;
    }

    &__tag {
      display: inline-block;
      padding: 10px;
      border: 1px solid var(--red-color);
      border-radius: 13px;
      font-family: ArialMT;
      font-weight: 400;
      font-size: 20px;
      line-height: 100%;
      color: var(--red-color);
      white-space: nowrap;
    }

    &__pagination {
      position: absolute;
      bottom: 0px !important;
      left: 0px;
      display: flex;
      gap: 20px;
      justify-content: flex-end;

      .swiper-pagination-bullet {
        width: 234px;
        height: 2px;
        border-radius: 2px;
        background-color: #d9d9d9;
        opacity: 1;
        transition: background-color 0.3s ease;

        &-active {
          background-color: var(--blue-color);
        }
      }
    }

    &__navigation {
      position: absolute;
      top: 50%;
      left: 0;
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      transform: translateY(-50%);
      pointer-events: none;
    }

    &__nav-prev,
    &__nav-next {
      pointer-events: auto;
      cursor: pointer;
      z-index: 5;
    }

    &__nav-prev {
      margin-left: -40px;
      transform: rotate(180deg);
    }

    &__nav-next {
      margin-right: -40px;
    }

    .btn-navigation {
      width: 24px;
      height: 24px;
      fill: var(--main-color);
      transition: fill 0.3s ease;

      &:hover {
        fill: var(--blue-color);
      }
    }
  }

  .licenses {
    margin-bottom: 100px;

    &__box {
      margin-top: 24px;
      background-color: var(--red-color);
      padding: 35px;
      border-radius: 25px;
      position: relative;

      &::after {
        content: "";
        position: absolute;
        top: -45px;
        right: 65px;
        width: 338px;
        height: 267px;
        background-image: url("../assets/img/licenses/list.png");
        background-repeat: no-repeat;
        background-size: contain;
        background-position: bottom right;
        pointer-events: none;
        z-index: 1;
      }
    }

    &__description {
      max-width: 534px;
      font-family: ArialMT;
      font-weight: 400;
      font-size: 18px;
      line-height: 100%;
      color: var(--white-color);
    }

    &__info {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      row-gap: 28px;
    }

    &__btn {
      font-family: Montserrat;
      font-weight: 500;
      font-size: 18px;
      line-height: 26px;
      color: var(--red-color);
      border: none;
      outline: none;
      transition: all 0.3s ease;
      border-radius: 30px;
      background: #f9f9f9;
      padding: 7.5px 20px;
    }

    &__img {
      display: none;
    }
  }

  .outfit {
    margin-bottom: 100px;

    &__box {
      margin-top: 30px;
      position: relative;
    }

    &__block {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
    }

    &__heading {
      font-family: Montserrat;
      font-weight: 700;
      font-size: 25px;
      line-height: 30px;
      color: var(--main-color);
      letter-spacing: -1px;
      margin-bottom: 24px;
      min-height: 60px;
      -webkit-line-clamp: 2;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      overflow: hidden;
      overflow-wrap: anywhere;
    }

    &__text {
      font-family: ArialMT;
      font-weight: 400;
      font-size: 20px;
      line-height: 22px;
      color: var(--main-color);
      margin-bottom: 45px;
      min-height: 100px;
      -webkit-line-clamp: 5;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      overflow: hidden;
      overflow-wrap: anywhere;
    }

    &__btn {
      display: inline-block;
      font-family: Montserrat;
      font-weight: 500;
      font-size: 18px;
      line-height: 26px;
      color: var(--white-color);
      border: none;
      outline: none;
      transition: all 0.3s ease;
      border-radius: 30px;
      background: var(--blue-color);
      padding: 10px 42px;

      &:hover {
        background: var(--blue-color-hover);
      }
    }

    &__info {
      min-width: 619px;
      max-width: 619px;
      max-height: 362px;
      min-height: 362px;
      height: 100%;
      padding: 38px 29px 39px 25px;
      background-color: var(--white-color);
      border-radius: 25px 0 0 25px;
    }

    &__img {
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      border-radius: 0 25px 25px 0;
      background-color: var(--background-img);

      img {
        width: 378px;
        height: 362px;
        object-fit: cover;
      }
    }

    &__slider {
      position: relative;
      overflow: hidden;
    }

    &__pagination {
      display: none !important;
      position: absolute;
      bottom: 0px !important;
      left: 0px;
      display: flex;
      gap: 20px;
      justify-content: flex-end;

      .swiper-pagination-bullet {
        width: 234px;
        height: 2px;
        border-radius: 2px;
        background-color: #d9d9d9;
        opacity: 1;
        transition: background-color 0.3s ease;

        &-active {
          background-color: var(--blue-color);
        }
      }
    }

    &__navigation {
      position: absolute;
      top: 50%;
      left: 0;
      width: 100%;
      display: flex;
      justify-content: space-between;
      transform: translateY(-50%);
      pointer-events: none;
    }

    &__nav-prev,
    &__nav-next {
      pointer-events: auto;
      cursor: pointer;
      z-index: 5;
    }

    &__nav-prev {
      margin-left: -40px;
      transform: rotate(180deg);
    }

    &__nav-next {
      margin-right: -40px;
    }

    .btn-navigation {
      width: 24px;
      height: 24px;
      fill: var(--main-color);
      transition: fill 0.3s ease;

      &:hover {
        fill: var(--blue-color);
      }
    }
  }

  .works {
    margin-bottom: 100px;

    &__box {
      margin-top: 35px;
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      gap: 40px;
    }

    &__img {
      max-width: 413px;

      img {
        width: 100%;
        height: auto;
        display: block;
      }
    }

    &__info {
      display: flex;
      flex-direction: column;
      gap: 40px;
      position: relative;
    }

    &__step {
      display: flex;
      align-items: flex-start;
      gap: 28px;
      position: relative;

      &-num {
        min-width: 60px;
        max-width: 60px;
        height: 60px;
        border: 1px solid var(--red-color);
        border-radius: 50%;
        color: var(--red-color);
        font-family: Montserrat;
        font-weight: 700;
        font-size: 25px;
        line-height: 25px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        z-index: 2;
        background-color: var(--white-color);
        margin-top: -18px;
      }

      &-content {
        max-width: 559px;
      }

      &-title {
        font-family: Montserrat;
        font-weight: 700;
        font-size: 25px;
        line-height: 25px;
        margin-bottom: 20px;
        color: var(--main-color);
      }

      &-text {
        font-family: ArialMT;
        font-weight: 400;
        font-size: 20px;
        line-height: 25px;
        color: var(--main-color);
      }
    }

    &__step:not(:last-child)::after {
      content: "";
      position: absolute;
      left: 30px;
      top: 35px;
      width: 2px;
      height: calc(100%);
      background-color: var(--red-color);
      z-index: 1;
    }
  }

  .feedback {
    margin-bottom: 110px;

    &__inner {
      border-radius: 25px;
      padding: 40px 35px 55px;
      background-color: var(--white-color);
      position: relative;
      overflow: hidden;

      &::after {
        content: "";
        position: absolute;
        bottom: 0;
        right: 0;
        width: 424px;
        height: 307px;
        background-image: url("../assets/img/tools/sun2.png");
        background-repeat: no-repeat;
        background-size: contain;
        background-position: bottom right;
        pointer-events: none;
      }
    }

    &__subtitle {
      margin-top: 10px;
      max-width: 598px;
      font-family: ArialMT;
      font-weight: 400;
      font-size: 20px;
      line-height: 25px;
      color: var(--main-color);
    }

    &__box {
      margin-top: 33px;
    }

    &__form {
      display: flex;
      flex-direction: column;
      row-gap: 50px;
    }

    &__fields {
      display: flex;
      gap: 34px;
      flex-wrap: wrap;
    }

    &__field {
      position: relative;
      display: flex;
      flex-direction: column;

      label {
        font-family: ArialMT;
        font-weight: 400;
        font-size: 20px;
        line-height: 25px;
        color: var(--main-color);
        margin-bottom: 5px;
      }

      input {
        width: 370px;
        padding: 12px 15px;
        border: 1px solid #0000000d;
        border-radius: 7px;
        background: #f9f9f9;
        font-size: 16px;
        font-family: ArialMT;
        font-weight: 400;
        font-size: 20px;
        line-height: 25px;
        color: var(--main-color);

        &:focus {
          border-color: var(--blue-color-hover);
          outline: none;
        }

        &::placeholder {
          color: #c9c9c9;
          opacity: 1;
        }
      }

      .pristine-error {
        position: absolute;
        bottom: -18px;
        left: 0;
        color: #f44747;
        font-family: ArialMT;
        font-weight: 400;
        font-size: 14px;
        line-height: 14px;
      }
    }

    &__bottom {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      gap: 20px;
    }

    &__btn {
      padding: 13px 33px;
      background-color: var(--blue-color);
      font-family: Montserrat;
      font-weight: 500;
      font-size: 18px;
      line-height: 26px;
      color: var(--white-color);
      border: none;
      border-radius: 30px;
      cursor: pointer;

      &:hover {
        background-color: var(--blue-color-hover);
      }
    }

    &__policy {
      width: 351px;
      font-family: ArialMT;
      font-weight: 400;
      font-size: 14px;
      line-height: 14px;
      color: var(--main-color);
      a {
        font-family: ArialMT;
        font-weight: 400;
        font-size: 14px;
        line-height: 14px;
        color: var(--red-color);
        text-decoration: underline;
      }
    }

    .has-error input {
      border-color: #f44747;
    }
  }

  .gallery {
    margin-bottom: 100px;

    &__block {
      position: relative;
    }

    &__box {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 30px;
    }

    &__slider {
      overflow: hidden;

      .swiper-slide {
        height: 292px;
        border-radius: 15px;
        overflow: hidden;

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          display: block;
        }
      }
    }

    &__pagination {
      display: none !important;
      position: absolute;
      bottom: 0px !important;
      left: 0px;
      display: flex;
      gap: 20px;
      justify-content: flex-end;

      .swiper-pagination-bullet {
        width: 234px;
        height: 2px;
        border-radius: 2px;
        background-color: #d9d9d9;
        opacity: 1;
        transition: background-color 0.3s ease;

        &-active {
          background-color: var(--blue-color);
        }
      }
    }

    &__navigation {
      position: absolute;
      top: 50%;
      left: 0;
      width: 100%;
      display: flex;
      justify-content: space-between;
      transform: translateY(-50%);
      pointer-events: none;
    }
    &__nav-prev,
    &__nav-next {
      pointer-events: auto;
      cursor: pointer;
      z-index: 5;
    }

    &__nav-prev {
      margin-left: -40px;
      transform: rotate(180deg);
    }

    &__nav-next {
      margin-right: -40px;
    }

    .btn-navigation {
      width: 24px;
      height: 24px;
      fill: var(--main-color);
      transition: fill 0.3s ease;

      &:hover {
        fill: var(--blue-color);
      }
    }
  }

  .specialists {
    margin-bottom: 110px;

    &__box {
      max-width: 100%;
      overflow: hidden;
      margin-top: 30px;
      position: relative;
      display: flex;
      justify-content: center;
      overflow: visible;
    }

    &__item {
      &-img {
        width: 100%;
        height: 359px;
        background-color: #f4f4f4;
        border-radius: 25px 25px 0 0;

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          border-radius: 25px 25px 0 0;
        }
      }

      &-info {
        padding: 20px 20px 25px;
        background-color: var(--white-color);
        border-radius: 0 0 25px 25px;
      }

      &-name {
        font-family: Montserrat;
        font-weight: 700;
        font-size: 20px;
        line-height: 25px;
        margin-bottom: 8px;
        height: 50px;
        color: var(--main-color);
        -webkit-line-clamp: 2;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
        overflow-wrap: anywhere;
      }

      &-profession {
        font-family: ArialMT;
        font-weight: 400;
        font-size: 16px;
        line-height: 16px;
        color: var(--blue-color);
        margin-bottom: 20px;
        -webkit-line-clamp: 2;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
        overflow-wrap: anywhere;
      }

      &-experience {
        display: inline-block;
        padding: 5px 4px;
        margin-bottom: 5px;
        border-radius: 5px;
        background-color: var(--red-color);
        font-family: ArialMT;
        font-weight: 400;
        font-size: 14px;
        line-height: 14px;
        color: var(--white-color);
      }

      &-category {
        font-family: ArialMT;
        font-weight: 400;
        font-size: 14px;
        line-height: 14px;
        color: var(--main-color);
        -webkit-line-clamp: 1;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
        overflow-wrap: anywhere;
      }

      &-btn {
        touch-action: manipulation;
        margin-top: 30px;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 13px 33px;
        background-color: var(--blue-color);
        font-family: Montserrat;
        font-weight: 500;
        font-size: 18px;
        line-height: 26px;
        color: var(--white-color);
        border: none;
        border-radius: 30px;
        cursor: pointer;

        &:hover {
          background-color: var(--blue-color-hover);
        }
      }
    }

    &__slider {
      width: 100%;
      max-width: 100%;
      overflow: hidden;
      position: relative;
    }

    &__navigation {
      position: absolute;
      top: 50%;
      left: 0;
      width: 100%;
      display: flex;
      justify-content: space-between;
      transform: translateY(-50%);
      pointer-events: none;

      .specialists__nav-prev,
