        margin-top: 15px;
        gap: 6px;
      }

      .btn-navigation {
        width: 8px;
        height: 15px;
      }

      &__navigation {
        margin-top: 0px;
      }

      &__nav-prev {
        margin-bottom: 0;
        display: flex;
        align-items: center;
      }
    }

    .works {
      &__box {
        margin-top: 45px;
        gap: 18px;
      }

      &__info {
        gap: 18px;
      }

      &__step {
        gap: 14px;

        &:not(:last-child)::after {
          content: "";
          position: absolute;
          left: 21px;
          top: 27px;
        }

        &-num {
          min-width: 44px;
          max-width: 44px;
          height: 44px;
          font-size: 18px;
          line-height: 20px;
          margin-top: -13px;
        }

        &-title {
          font-size: 14px;
          line-height: 16px;
          margin-bottom: 10px;
        }

        &-text {
          font-size: 14px;
          line-height: 16px;
        }
      }
    }

    .feedback {
      &__inner {
        padding: 25px 10px;
      }

      &__title {
        font-size: 21px;
        line-height: 26px;
      }

      &__subtitle {
        margin-top: 10px;
        font-size: 14px;
        line-height: 16px;
      }

      &__box {
        margin-top: 15px;
      }

      &__form {
        row-gap: 10px;
      }

      &__fields {
        gap: 10px;
      }

      &__field {
        width: 100%;

        label {
          font-size: 14px;
          line-height: 16px;
        }

        input {
          width: 100%;
          font-size: 14px;
          line-height: 16px;
          padding: 8px 15px;
        }
      }

      &__bottom {
        flex-wrap: wrap-reverse;
        gap: 30px;
      }

      &__policy {
        width: 100%;
        font-size: 12px;
        line-height: 14px;

        a {
          font-size: 12px;
          line-height: 14px;
        }
      }
    }

    .gallery {
      margin: 0 -15px 100px;

      &__title {
        padding: 0 15px;
      }

      &__box {
        margin-top: 25px;
        margin-left: 15px;
        overflow: visible;
      }

      .swiper-pagination {
        margin-top: 15px;
        width: 84% !important;
        gap: 6px;
      }

      .btn-navigation {
        width: 8px;
        height: 15px;
      }

      &__navigation {
        margin-top: 0px;
      }

      &__nav-prev {
        margin-bottom: 0;
        margin-left: 14px;
        display: flex;
        align-items: center;
      }

      &__nav-next {
        margin-right: 10px;
      }
    }

    .specialists {
      margin: 0 -15px 90px;

      &__title {
        padding: 0 15px;
      }

      &__link {
        display: none;
      }

      &__box {
        margin-top: 25px;
        margin-left: 15px;
      }

      &__item {
        &-img {
          width: 100%;
          height: 205px;
          border-radius: 15px 15px 0 0;
        }

        &-info {
          padding: 6px 10px 12px;
          border-radius: 0 0 15px 15px;
        }

        &-name {
          font-size: 12px;
          line-height: 14px;
          margin-bottom: 4px;
          height: 28px;
        }

        &-profession {
          font-size: 10px;
          line-height: 12px;
          margin-bottom: 13px;
        }

        &-experience {
          font-size: 9px;
          line-height: 12px;
          padding: 2px;
          border-radius: 3px;
          margin-bottom: 4px;
        }

        &-category {
          font-size: 10px;
          line-height: 12px;
        }

        &-btn {
          margin-top: 16px;
          padding: 6.5px 30px;
          font-size: 12px;
          line-height: 17px;
          border-radius: 20px;
        }
      }
    }

    .recording {
      margin-bottom: 100px;

      &__inner {
        padding: 20px 10px;
      }

      &__title {
        font-size: 21px;
        line-height: 26px;
      }

      &__subtitle {
        margin-top: 10px;
        max-width: 100%;
        font-size: 14px;
        line-height: 16px;
      }

      &__form {
        display: flex;
        flex-direction: row;
        row-gap: unset;
        gap: unset;
        flex-wrap: wrap;
      }

      &__box {
        margin-top: 20px;
        width: 100%;
      }

      &__field {
        order: 0;
        width: 100%;

        label {
          font-size: 14px;
          line-height: 16px;
        }

        input {
          width: 100%;
          font-size: 14px;
          line-height: 16px;
          padding: 8px 15px;
        }
      }

      &__policy {
        width: 100%;
        font-size: 12px;
        line-height: 14px;
        margin-bottom: 30px;

        a {
          font-size: 12px;
          line-height: 14px;
        }
      }

      &__fields {
        width: 100%;
        align-items: end;
        gap: 10px;
      }

      &__btn {
        padding: 7px 20px;
      }

      &__submit {
        order: 1;
      }
    }

    .modal {
      &__content {
        padding: 23px 11px 31px 10px;
      }

      &__close {
        top: 26px;
        right: 16px;

        .close {
          width: 16px;
          height: 16px;
        }
      }

      &__title {
        font-size: 21px;
        line-height: 23px;
        margin-bottom: 10px;
      }

      &__subtitle {
        max-width: unset;
        font-size: 14px;
        line-height: 16px;
        margin-bottom: 24px;
      }

      &__field {
        margin-bottom: 21px;

        &:last-of-type {
          margin-bottom: 28px;
        }

        label {
          font-size: 14px;
          line-height: 16px;
        }

        input {
          width: 269px;
          font-size: 14px;
          line-height: 16px;
          padding: 8px 15px;
        }

        .pristine-error {
          position: absolute;
          bottom: -18px;
          left: 0;
          color: #f44747;
          font-size: 12px;
        }
      }

      &__policy {
        width: 100%;
        font-size: 12px;
        line-height: 14px;
        margin-bottom: 30px;

        a {
          font-size: 12px;
          line-height: 14px;
        }
      }

      &__btn {
        padding: 7.5px 33px;
      }
    }
  }
}
